import React, { Component } from "react";
import CharacterPics from "./components/CharacterPics";
import Wrapper from "./components/Wrapper";
import characters from "./characters.json";

var newArray = []
class Game extends Component {
    

    state = {
        characters,
    };

    randomize = (newArray) => {
        for (var i = 0; i < newArray.length; i++) {
            var currentIndex = newArray.length, tempArray, randomIndex;
            while (0 !== currentIndex) {
                randomIndex = Math.floor(Math.random() * currentIndex);
                currentIndex -= 1;
                tempArray = newArray[currentIndex];
                newArray[currentIndex] = newArray[randomIndex];
                newArray[randomIndex] = tempArray;
            }
        }
    };

    updateScore = () => {

    }

    characterSelect = (id) => {
        console.log(id);
        newArray = this.state.characters.map(character => {
            if (character.id === id) {
                character.selected = true;
            }
            return character
        })
        this.setState({
            characters: newArray
        })
        console.log(this.state.characters)
        this.randomize(newArray);
    };

   

    render() {
        return (
            <Wrapper>
                {this.state.characters.map(characters => (
                    <CharacterPics
                        characterSelect={this.characterSelect}
                        id={characters.id}
                        key={characters.id}
                        image={characters.image}
                        selected={characters.selected}
                    />
                ))}
            </Wrapper>
        );
    }
}

export default Game;
